import React, { useState, useRef } from 'react';
import { InstagramStreamer } from 'react-instagram-streamer';
import './index.css';

const Demo = (props) => {
	const inputRef = useRef();
  const [username, setUsername] = useState('arunyokesh');

	const updateUsername = () => {
		const instaName = inputRef.current.value;
		setUsername(instaName ? instaName : '')
	}

	return (
		<>
			<h1 className="header">React Instagram Streamer Demo</h1>
			<div className="container">
				<input type="text" placeholder="Enter your instagram usename" value={username} ref={inputRef} />
				<button onClick={() => updateUsername()}>Get Images</button>
			</div>
			<div className="container">
				<InstagramStreamer username={username} />
			</div>
		</>
	);

}

export default Demo;